<template>
  <div class="allTile">
    <span>您的位置：</span>
    <span v-for="item in indexText"> {{item}} ></span>
  </div>
</template>

<script>
    export default {
        props:['indexText'],
        name: "pageNavigation"
    }
</script>

<style scoped>
  .allTile{
      width: 100%;
     overflow: hidden;
  }
  .allTile{
    font-size: 14px;
    color: #999;
  }
  @media screen and (max-width: 768px){
    .allTile{
      font-size: 12px;
      color: #999;
    }
  }
</style>



